export function warpImage(img, dstCoords, canvas,rotationAngle) {

    // Use naturalWidth and naturalHeight throughout
    var imgWidth = img.naturalWidth;
    var imgHeight = img.naturalHeight;

    // Define source coordinates (corners of the source image)
    var srcCoords = [
        { x: 0, y: 0 },
        { x: imgWidth, y: 0 },
        { x: imgWidth, y: imgHeight },
        { x: 0, y: imgHeight }
    ];


    const radians =  -rotationAngle;
    const cosA = Math.cos(radians);
    const sinA = Math.sin(radians);

    // Rotate source points around the center
    let [centerX,centerY] =  getCenterFromCorners(srcCoords);
    if(rotationAngle) {
        srcCoords = srcCoords.map(({x, y}) => {
            const dx = x - centerX;
            const dy = y - centerY;
            const xRot = dx * cosA - dy * sinA + centerX;
            const yRot = dx * sinA + dy * cosA + centerY;
            return {x: xRot, y: yRot};
        });
    }


    // Compute the homography matrix H that maps srcCoords to dstCoords
    var H = computeHomography(srcCoords, dstCoords);

    // Calculate the bounding box of the destination coordinates

    var dminX = Math.min(...dstCoords.map(p => p.x));
    var dmaxX = Math.max(...dstCoords.map(p => p.x));
    var dminY = Math.min(...dstCoords.map(p => p.y));
    var dmaxY = Math.max(...dstCoords.map(p => p.y));

    let reswidth = dmaxX - dminX;
    let resheight = dmaxY - dminY;

    canvas.width = Math.ceil(reswidth);
    canvas.height = Math.ceil(resheight);

    // Prepare the inverse homography for reverse mapping
    var invH = invertHomography(H);

    // Draw the source image onto a temporary canvas to access pixel data
    var tempCanvas = document.createElement('canvas');
    tempCanvas.width = imgWidth;
    tempCanvas.height = imgHeight;
    var tempCtx = tempCanvas.getContext('2d');
    tempCtx.drawImage(img, 0, 0, imgWidth, imgHeight);
    var srcImageData = tempCtx.getImageData(0, 0, imgWidth, imgHeight);
    var srcData = srcImageData.data;

    // Get destination canvas context and image data
    var dstCtx = canvas.getContext('2d');
    var dstImageData = dstCtx.createImageData(canvas.width, canvas.height);
    var dstData = dstImageData.data;

    // Process each pixel in the destination image
    for (var y = 0; y < canvas.height; y++) {
        for (var x = 0; x < canvas.width; x++) {
            // Map the pixel position to the corresponding position in the source image
            var destX = x + dminX;
            var destY = y + dminY;

            var srcPos = applyHomography(invH, destX, destY);

            var xs = srcPos.x;
            var ys = srcPos.y;

            // Check if the source position is within the source image bounds
            if (xs >= 0 && xs < imgWidth && ys >= 0 && ys < imgHeight) {
                // Use bilinear interpolation to sample the pixel
                var color = sampleBilinear(srcData, imgWidth, imgHeight, xs, ys);

                // Set the pixel in the destination image
                var index = (y * canvas.width + x) * 4;
                dstData[index] = color[0];
                dstData[index + 1] = color[1];
                dstData[index + 2] = color[2];
                dstData[index + 3] = color[3];
            }
        }
    }

    dstCtx.putImageData(dstImageData, 0, 0);
}

function getCenterFromCorners(corners) {
    // Top-left corner is the first element
    const topLeft = corners[0];
    // Bottom-right corner is the third element
    const bottomRight = corners[2];
    const centerX = (topLeft.x + bottomRight.x) / 2;
    const centerY = (topLeft.y + bottomRight.y) / 2;
    return [ centerX, centerY ];
}

function computeHomography(src, dst) {
    // Set up matrix equations to solve for homography
    var A = [];
    for (var i = 0; i < 4; i++) {
        var xs = src[i].x;
        var ys = src[i].y;
        var xd = dst[i].x;
        var yd = dst[i].y;

        A.push([-xs, -ys, -1, 0, 0, 0, xs * xd, ys * xd, xd]);
        A.push([0, 0, 0, -xs, -ys, -1, xs * yd, ys * yd, yd]);
    }

    // Solve Ah = 0 using Singular Value Decomposition (SVD)
    var h = solveHomography(A);

    // Reshape h into a 3x3 matrix
    var H = [
        [h[0], h[1], h[2]],
        [h[3], h[4], h[5]],
        [h[6], h[7], h[8]]
    ];

    return H;
}

function solveHomography(A) {
    // Use SVD to solve Ah = 0
    // For simplicity, we use numeric.js library functions
    // Since we can't use external libraries, we'll use a simple approximation

    // Normally, you would perform SVD here, but we'll approximate for simplicity
    // We'll set h[8] = 1 and solve the linear system
    var a = [];
    var b = [];
    for (var i = 0; i < A.length; i++) {
        a.push(A[i].slice(0, 8));
        b.push(-A[i][8]);
    }

    // Solve for h[0..7]
    var h = solveLinearSystem(a, b);
    h.push(1); // h[8] = 1

    return h;
}

function solveLinearSystem(A, b) {
    // Simple Gaussian elimination solver
    var m = A.length;
    var n = A[0].length;

    // Augment A with b
    for (var i = 0; i < m; i++) {
        A[i].push(b[i]);
    }

    // Forward elimination
    for (var k = 0; k < n; k++) {
        // Find the pivot row
        var maxRow = k;
        var maxVal = Math.abs(A[k][k]);
        for (var i = k + 1; i < m; i++) {
            if (Math.abs(A[i][k]) > maxVal) {
                maxVal = Math.abs(A[i][k]);
                maxRow = i;
            }
        }

        // Swap rows
        if (maxRow !== k) {
            var temp = A[k];
            A[k] = A[maxRow];
            A[maxRow] = temp;
        }

        // Eliminate below
        for (var i = k + 1; i < m; i++) {
            var c = A[i][k] / A[k][k];
            for (var j = k; j <= n; j++) {
                A[i][j] -= c * A[k][j];
            }
        }
    }

    // Back substitution
    var x = new Array(n);
    for (var i = n - 1; i >= 0; i--) {
        x[i] = A[i][n] / A[i][i];
        for (var k = i - 1; k >= 0; k--) {
            A[k][n] -= A[k][i] * x[i];
        }
    }

    return x;
}

function invertHomography(H) {
    // Compute the inverse of a 3x3 matrix
    var det =
        H[0][0] * (H[1][1] * H[2][2] - H[2][1] * H[1][2]) -
        H[0][1] * (H[1][0] * H[2][2] - H[2][0] * H[1][2]) +
        H[0][2] * (H[1][0] * H[2][1] - H[2][0] * H[1][1]);

    if (det === 0) {
        throw new Error("Homography matrix is singular and cannot be inverted.");
    }

    var invDet = 1 / det;

    var invH = [
        [
            (H[1][1] * H[2][2] - H[2][1] * H[1][2]) * invDet,
            (H[0][2] * H[2][1] - H[0][1] * H[2][2]) * invDet,
            (H[0][1] * H[1][2] - H[0][2] * H[1][1]) * invDet
        ],
        [
            (H[1][2] * H[2][0] - H[1][0] * H[2][2]) * invDet,
            (H[0][0] * H[2][2] - H[0][2] * H[2][0]) * invDet,
            (H[1][0] * H[0][2] - H[0][0] * H[1][2]) * invDet
        ],
        [
            (H[1][0] * H[2][1] - H[2][0] * H[1][1]) * invDet,
            (H[2][0] * H[0][1] - H[0][0] * H[2][1]) * invDet,
            (H[0][0] * H[1][1] - H[1][0] * H[0][1]) * invDet
        ]
    ];

    return invH;
}

function applyHomography(H, x, y) {
    var denom = H[2][0] * x + H[2][1] * y + H[2][2];
    var xs = (H[0][0] * x + H[0][1] * y + H[0][2]) / denom;
    var ys = (H[1][0] * x + H[1][1] * y + H[1][2]) / denom;
    return { x: xs, y: ys };
}

function sampleBilinear(data, width, height, x, y) {
    var x0 = Math.floor(x);
    var y0 = Math.floor(y);
    var x1 = x0 + 1;
    var y1 = y0 + 1;

    var dx = x - x0;
    var dy = y - y0;

    var index = function (xi, yi) {
        xi = Math.max(0, Math.min(width - 1, xi));
        yi = Math.max(0, Math.min(height - 1, yi));
        return (yi * width + xi) * 4;
    };

    var c00 = getPixel(data, index(x0, y0));
    var c10 = getPixel(data, index(x1, y0));
    var c01 = getPixel(data, index(x0, y1));
    var c11 = getPixel(data, index(x1, y1));

    var c0 = interpolateColor(c00, c10, dx);
    var c1 = interpolateColor(c01, c11, dx);
    var c = interpolateColor(c0, c1, dy);

    return c;
}

function getPixel(data, index) {
    return [data[index], data[index + 1], data[index + 2], data[index + 3]];
}

function interpolateColor(c0, c1, t) {
    var c = [];
    for (var i = 0; i < 4; i++) {
        c[i] = c0[i] * (1 - t) + c1[i] * t;
    }
    return c;
}
