<svelte:options tag="fds-image-transform-corners"></svelte:options>
<script>
  import {warpImage} from './warpImage'
  import numeric from 'numeric';
  import {onMount} from 'svelte'


  export let imageurl = '';
  let imageel;
  export let position;
  export let resultCanvas;
  export let resultdim;
  export let layerimagedata;
  export let zoom = 100;
  export let ratio = window.devicePixelRatio;
  export let opacity = 100;
  if (globalThis.gyre && globalThis.gyre.paletteValues.selectedLayer) opacity = globalThis.gyre.paletteValues.selectedLayer.opacity;
  if (globalThis.gyre) layerimagedata = globalThis.gyre.paletteValues.selectedLayer;
  if (globalThis.gyre) zoom = globalThis.gyre?.canvas.zoom;
  if (globalThis.gyre) imageurl = layerimagedata.url;

  let p = parseFloat(zoom) / 100 / ratio

  // Rotation angle in degrees
  let rotationAngle = 0;

  // Initial corner positions (unrotated)
  let originalCorners = [];

  let noscalecorners = [];

  // User-adjustable corners (initialize to originalCorners)
  let corners = [...originalCorners];

  let isDragging = false;
  let selectedCorner = null;
  let container;
  let transformMatrix = ''; // Reactive variable for the transform

  function reCalc(num) {
    let newCoord = num / p;
    return newCoord
  }


  $: if (globalThis.gyre?.canvas.zoom) console.log("zoooom", globalThis.gyre?.canvas.zoom);

  $: if (container && !position && layerimagedata){
    position = {}
    setPosition();
    update();
  }

/*
  $: if (container && position) {

  }
*/

  onMount(() => {
    resultCanvas = document.createElement('canvas');

  })


  export function refresh() {
    if (globalThis.gyre && globalThis.gyre.paletteValues.selectedLayer) opacity = globalThis.gyre.paletteValues.selectedLayer.opacity;
    if (globalThis.gyre) layerimagedata = globalThis.gyre.paletteValues.selectedLayer;
    if (globalThis.gyre) zoom = globalThis.gyre?.canvas.zoom;
    if (globalThis.gyre) imageurl = layerimagedata.url;
    p = parseFloat(zoom) / 100 / ratio
    setPosition();
    update();
  }

  function setPosition() {
    position.x = layerimagedata._x;
    position.y = layerimagedata._y;
    position.width = layerimagedata._width;
    position.height = layerimagedata._height;
  }

  function update() {

    positionToCorners();
    updateTransform();
    container.style.top = layerimagedata._y + "px";
    container.style.left = layerimagedata._x + "px";
    container.style.width = layerimagedata._width + "px";
    container.style.height = layerimagedata._height + "px";
  }


  function positionToCorners() {
    originalCorners = [
      {x: 0, y: 0},        // top-left
      {x: position.width, y: 0},      // top-right
      {x: position.width, y: position.height},    // bottom-right
      {x: 0, y: position.height}       // bottom-left
    ];


    if(noscalecorners.length){
      corners = noscalecorners.map((corner) =>{
                       return {x:(corner.x*p), y:(corner.y*p)}
      });
    } else {
      corners = [...originalCorners];
    }


    noscalecorners = [
      {x: 0, y: 0},        // top-left
      {x: parseInt(position.width/p), y: 0},      // top-right
      {x: parseInt(position.width/p), y: parseInt(position.height/p)},    // bottom-right
      {x: 0, y: parseInt(position.height/p)}       // bottom-left
    ];


    rotationAngle = position.rotation || 0;
  }


  function getCenterFromCorners(corners) {
    // Top-left corner is the first element
    const topLeft = corners[0];
    // Bottom-right corner is the third element
    const bottomRight = corners[2];
    const centerX = (topLeft.x + bottomRight.x) / 2;
    const centerY = (topLeft.y + bottomRight.y) / 2;
    return [centerX, centerY];
  }

  // Helper function to calculate the perspective transformation matrix
  function getPerspectiveTransformMatrix(src, dst, angle) {

    // Convert angle to radians
    const radians = -angle;
    //const radians = (Math.PI / 180) * angle;
    const cosA = Math.cos(radians);
    const sinA = Math.sin(radians);

    // Rotate source points around the center
    let [centerX, centerY] = getCenterFromCorners(src);

    const rotatedSrc = src.map(({x, y}) => {
      const dx = x - centerX;
      const dy = y - centerY;
      const xRot = dx * cosA - dy * sinA + centerX;
      const yRot = dx * sinA + dy * cosA + centerY;
      return {x: xRot, y: yRot};
    });

    const matrix = [];
    for (let i = 0; i < 8; i++) matrix[i] = [];

    for (let i = 0; i < 4; i++) {
      matrix[i][0] = matrix[i + 4][3] = rotatedSrc[i].x;
      matrix[i][1] = matrix[i + 4][4] = rotatedSrc[i].y;
      matrix[i][2] = matrix[i + 4][5] = 1;
      matrix[i][3] = matrix[i][4] = matrix[i][5] = 0;
      matrix[i + 4][0] = matrix[i + 4][1] = matrix[i + 4][2] = 0;
      matrix[i][6] = -rotatedSrc[i].x * dst[i].x;
      matrix[i][7] = -rotatedSrc[i].y * dst[i].x;
      matrix[i + 4][6] = -rotatedSrc[i].x * dst[i].y;
      matrix[i + 4][7] = -rotatedSrc[i].y * dst[i].y;
    }

    const results = [
      dst[0].x,
      dst[1].x,
      dst[2].x,
      dst[3].x,
      dst[0].y,
      dst[1].y,
      dst[2].y,
      dst[3].y
    ];

    // Solving the system of linear equations to find the transformation matrix
    const h = numeric.solve(matrix, results).concat(1);

    return `matrix3d(${h[0]}, ${h[3]}, 0, ${h[6]}, ${h[1]}, ${h[4]}, 0, ${h[7]}, 0, 0, 1, 0, ${h[2]}, ${h[5]}, 0, 1)`;
  }


  function handleMouseDown(index, event) {
    isDragging = true;
    selectedCorner = index;
  }

  function handleMouseUp() {
    isDragging = false;
    selectedCorner = null;
  }

  function handleMouseMove(event) {
    if (!isDragging || selectedCorner === null) return;

    const rect = container.getBoundingClientRect();
    const x = event.clientX - rect.left;
    const y = event.clientY - rect.top;

    noscalecorners = noscalecorners.map((corner, index) =>
            index === selectedCorner
                    ? {x:(x/p), y:(y/p)}
                    : corner
    );

    corners = corners.map((corner, index) =>
            index === selectedCorner
                    ? {x, y}
                    : corner
    );

    // Update the transform matrix when the corners are moved
    updateTransform();
  }

  function updateTransform() {
    const transform = getPerspectiveTransformMatrix(originalCorners, corners, rotationAngle);
    transformMatrix = transform;
  }


  function makeImage() {

    let scaledcorners = corners.map((el) => {
      el.x = reCalc(el.x)
      el.y = reCalc(el.y)
      return el;
    })
    warpImage(image, scaledcorners, resultCanvas, rotationAngle);
    var dminX = Math.min(...scaledcorners.map(p => p.x));
    var dmaxX = Math.max(...scaledcorners.map(p => p.x));
    var dminY = Math.min(...scaledcorners.map(p => p.y));
    var dmaxY = Math.max(...scaledcorners.map(p => p.y));

    resultdim = {x: dminX, y: dminY, width: dmaxX - dminX, height: dmaxY - dminY}

  }

  let image

  // Update transform when rotationAngle changes
  $: if (rotationAngle) updateTransform();


  export function csstoimage() {
    makeImage();
  }


</script>

<style>

  .container {
    position: absolute;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    transform: rotate(0rad);
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transform-origin: top left;
    user-select: none;
  }

  image {
    transform-origin: top left;
  }

  .corner {
    position: absolute;
    width: 10px;
    height: 10px;
    background-color: red;
    border-radius: 50%;
    cursor: pointer;
    user-select: none;
  }
</style>

<div bind:this={container} class="container">
  <div style="position: relative; width: 100%; height: 100% ">
    <!-- Apply the transformation to the image -->
    <img style="opacity:{(opacity/100)}; transform: {transformMatrix};" bind:this={imageel} src={imageurl}>

    {#each corners as {x, y}, index}
      <div
              class="corner"
              style="left: {x - 5}px; top: {y - 5}px;"
              on:mousedown={() => handleMouseDown(index)}
      ></div>
    {/each}
  </div>
</div>

<!-- Optional rotation control -->


<!--
<input type="range" min="0" max="360" bind:value={rotationAngle} style="margin-top:200px" />
<button on:click={makeImage} width="800" height="450">Make Image</button>
-->


<img
        bind:this={image}
        src="{imageurl}"
        style="visibility:hidden"
/>
<svelte:window
        on:pointermove={handleMouseMove}
        on:pointerup={handleMouseUp}
/>