<svelte:options tag="fds-image-transform"></svelte:options>
<script>
    /*
      A freely user-transformable wrapper component.

      Size, position and rotation can be adjusted via handles or multi-touch gesture.
      Place content in the default slot.
      Handles can be overridden via the "handle" and "rotator" slots.

      The immediate parent must be a positioned element
      (i.e. styled with "position: relative/absolute/fixed/etc").
    */

    import {onMount} from 'svelte'
    import vector from './vector.js'
    import {createEventDispatcher, tick} from 'svelte';
    import {toollayer} from "./stores";

    let endDrag = false;
    let newlayer
    import {get_current_component} from "svelte/internal";
    import magneticLinesClass from "./magneticLines.js"
    // eslint-disable-next-line no-unused-vars
    import FourCornersTransformation from "./FourCornersTransformation.svelte"

    let host = get_current_component()
    // eslint-disable-next-line no-unused-vars
    import Dialog from './Dialog.svelte'
    // eslint-disable-next-line no-unused-vars
    import Dialogf from './DialogFourCorners.svelte'

    const svelteDispatch = createEventDispatcher()

    const dispatch = (name, detail) => {
        detail = internalposition;
        $toollayer = internalposition;
        detail.endDrag = !!endDrag;
        if (!!endDrag) {
            endDrag = !endDrag;
        }
        name = 'transformchange';
        // console.log("dispatch",name,detail);
        svelteDispatch(name, detail);
        host.dispatchEvent &&
        host.dispatchEvent(new CustomEvent(name, {detail}));
    };


    // the position at the start of each gesture
    // transformations are calculated relative to this
    let startPosition = {
        x: null,
        y: null,
        width: null,
        height: null,
        rotation: null
    }

    $: if($toollayer){
        refresh();
    }

    export function refresh() {
        setInitalPosition();
        internalposition = $toollayer;
    }



    // props
    //export let position = { x: 0, y: 0, w: 0, h: 0, r: 0 }
    // Magnetic tolerance distance for snapping
    const magneticTolerance = 10;

    // Layers array to store all layers
    export let layers = []

    export let demo = false
    export let layerimagedata = {};
    export let internalposition = startPosition;
    export let position;
    export let target = null
    export let container = null
    export let rotatorEnabled = true // Show a separate handle for rotation
    export let rotatorBar = false // Draw a line from the rotator handle to the frame
    export let handleMode = 'resize' // 'resize' or 'rotate'. Controls corner handle behaviour
    export let lockAspect = false // Is the aspect ratio fixed?
    export let lockTouchResize = false // Can the size be changed by multitouch gestures?
    export let captureClicks = true // allow clicks on the content area? if false, clicks fall through
    export let zoom = 100;
    export let ratio = window.devicePixelRatio;
    export let imageurl;
    export let imagesvg;
    export let svgcolor;
    // primitives
    export let type;
    export let initialposition;

    let lastzoom
    let imageel;
    let transformArea;
    let transformInProgress = false
    let magneticLines = [];


    function setInitalPosition(){
        if(!initialposition && internalposition.x!==null) {initialposition=noZoomPosition({...internalposition});}

    }

    function addMagneticLine(side, linePosition, lineOrientation, box) {
        let ml = new magneticLinesClass()
        let line = ml.calcMagneticLine(side, linePosition, lineOrientation, box)
        magneticLines.push(line)
    }

    // Snapping helpers
    function getClosestSnappingPosition(layers, newPos, tolerance) {
        let snappedPosition = {...newPos};

        layers.forEach(layer => {
            if (layer !== internalposition) {
                // Horizontal snapping (x-axis)
                if (Math.abs(newPos.x - layer.x) < tolerance) {
                    addMagneticLine("left", "side", "vertical", layer.originalData)
                    snappedPosition.x = layer.x
                } else if (Math.abs((newPos.x + newPos.width) - (layer.x + layer.width)) < tolerance) {
                    addMagneticLine("right", "side", "vertical", layer.originalData)
                    snappedPosition.x = layer.x + layer.width - newPos.width
                } else if (Math.abs((newPos.x + newPos.width / 2) - (layer.x + layer.width / 2)) < tolerance) {
                    addMagneticLine("", "center", "vertical", layer.originalData)
                    snappedPosition.x = layer.x + layer.width / 2 - newPos.width / 2
                } else if (Math.abs((newPos.x) - (layer.x + layer.width)) < tolerance) {
                    snappedPosition.x = layer.x + layer.width
                    addMagneticLine("right", "side", "vertical", layer.originalData)
                } else if (Math.abs((newPos.x + newPos.width) - (layer.x)) < tolerance) {
                    snappedPosition.x = layer.x - newPos.width
                    addMagneticLine("left", "side", "", layer.originalData)
                }
                // console.log(Math.abs(newPos.y-layer.y))
                // Vertical snapping (y-axis)
                if (Math.abs(newPos.y - layer.y) < tolerance) {
                    snappedPosition.y = layer.y
                    addMagneticLine("top", "side", "", layer.originalData)
                } else if (Math.abs((newPos.y + newPos.height) - (layer.y + layer.height)) < tolerance) {
                    snappedPosition.y = layer.y + layer.height - newPos.height
                    addMagneticLine("bottom", "side", "", layer.originalData)
                } else if (Math.abs((newPos.y + newPos.height / 2) - (layer.y + layer.height / 2)) < tolerance) {
                    snappedPosition.y = layer.y + layer.height / 2 - newPos.height / 2
                    addMagneticLine("", "center", "horizontal", layer.originalData)
                } else if (Math.abs((newPos.y) - (layer.y + layer.height)) < tolerance) {
                    snappedPosition.y = layer.y + layer.height
                    addMagneticLine("bottom", "side", "", layer.originalData)
                } else if (Math.abs((newPos.y + newPos.height) - (layer.y)) < tolerance) {
                    snappedPosition.y = layer.y - newPos.height
                    addMagneticLine("top", "side", "", layer.originalData)
                }
            }
        })

        return snappedPosition
    }

    // Add logic to detect same rotation layers
    function getClosestSnappingPositionWithRotation(layers, newPos, tolerance) {
        let snappedPosition = {...newPos}
        let noRotationLayers = [];
        let sameRotationLayers = [];

        // Check if the current rotation ot matche any other layer's rotation or is  0
        layers.forEach(layer => {
            if (layer !== internalposition && (layer.rotation !== internalposition.rotation || layer.rotation === 0)) {
                let l = JSON.parse(JSON.stringify(layer))
                l.rotation = 0
                l.originalData = JSON.parse(JSON.stringify(l))
                noRotationLayers.push(l)
            }
        });
        // First, do snapping like before
        snappedPosition = getClosestSnappingPosition(noRotationLayers, newPos, tolerance)

        // Check if the current rotation matches any other layer's rotation and isn't 0
        layers.forEach(layer => {
            if (layer !== internalposition && layer.rotation === internalposition.rotation && layer.rotation !== 0) {
                let l = JSON.parse(JSON.stringify(layer))
                l.originalData = JSON.parse(JSON.stringify(layer)) // add box as it shows on the screen with rotation
                sameRotationLayers.push(l)
            }
        });

        // If we have layers with the same rotation, normalize their positions
        if (sameRotationLayers.length > 0) {
            let rotation = sameRotationLayers[0].rotation
            sameRotationLayers.push(JSON.parse(JSON.stringify(newPos)))
            let ml = new magneticLinesClass()
            ml.rotateBoxesToZero(sameRotationLayers, rotation)      // make scenario as rotation is set to 0 for easier snapping logic
            let normalizedCurrentPos = sameRotationLayers.pop()
            // Perform snapping on normalized positions
            let normalizedSnappedPosition = getClosestSnappingPosition(sameRotationLayers, normalizedCurrentPos, tolerance, rotation)
            // Rotate back the positions
            if (magneticLines.length) snappedPosition = ml.rotatePositionBackToOriginal(normalizedSnappedPosition, rotation)
        }

        return snappedPosition
    }


    // Target element is this component (so apply calculated style directly)
    let selfStyled = false
    $: {
        selfStyled = target === element
    }

    $: if (imagesvg && svgcolor) {
        imagesvg = imagesvg.replace("<svg ", `<svg style="fill:${svgcolor}" `);
        layerimagedata.url = imageurl = "data:image/svg+xml;base64," + window.btoa(unescape(encodeURIComponent(imagesvg)));
    }

    $: if (imagesvg && lockAspect) {

        imagesvg = imagesvg.replace(`preserveAspectRatio="none"`, ` `);
        imagesvg = imagesvg.replace(`class="fullwidthheight"`, ` `);

        layerimagedata.url = imageurl = "data:image/svg+xml;base64," + window.btoa(unescape(encodeURIComponent(imagesvg)));
    }

    $: if (imagesvg && !lockAspect) {
        imagesvg = imagesvg.replace("<svg ", `<svg preserveAspectRatio="none" class="fullwidthheight" `);
        layerimagedata.url = imageurl = "data:image/svg+xml;base64," + window.btoa(unescape(encodeURIComponent(imagesvg)));
    }

    // DOMRects of the component and parent element, and the FreeTransform element itself
    let containerBounds, bounds, selfBounds
    // Map of active pointers
    const pointers = {}
    // Number of active pointers
    let pointerCount = 0
    // References to DOM elements
    let element, handleTR, handleTL, handleBR, handleBL, rotatorHandle
    $: if (mounted && imageel && element && layerimagedata && layerimagedata.url) {
        if (!target) target = element
        if (!container) container = target.parentNode
        initImage();
    }

    onMount(() => {
        mounted = true;
        if (!imageel) return
        initImage();
        setInitalPosition();
    })

    let imagealreadyinit
    let mounted

    function initImage() {

        if (imagealreadyinit) return;
        imagealreadyinit = true;
        imageel.onload = () => {
            internalposition = {
                x: imageel.x,
                y: imageel.y,
                width: imageel.naturalWidth,
                height: imageel.naturalHeight,
                rotation: 0
            }
            recalculatePosition();
            onBoundsChange();
        }
        if (!target) target = element
        if (!container) container = target.parentNode
    }

    function onKeyUp(e) {
        if (e.keyCode === 16) {
            //console.log('Shift key is released');
            lockAspect = false;
        }
    }


    function onKeyDown(e) {
        if (e.keyCode === 16) {
            //console.log('Shift key is released');
            lockAspect = true;
        }
    }

    function noZoomPosition(obj) {
        let retobj = {};
        retobj = {...obj};
        retobj.x = reCalc(obj.x,'rev');
        retobj.y = reCalc(obj.y,'rev');
        retobj.width = reCalc(obj.width,'rev');
        retobj.height = reCalc(obj.height,'rev');
        return retobj;
    }

    function recalculatePosition() {


        if (layerimagedata?._x || layerimagedata?._x == 0) internalposition.x = layerimagedata._x;
        if (layerimagedata?._y || layerimagedata?._y == 0) internalposition.y = layerimagedata._y;
        if (layerimagedata?._width) internalposition.width = (layerimagedata._width);
        if (layerimagedata?._height) internalposition.height = (layerimagedata._height);


        if (layerimagedata?.x || layerimagedata?.x == 0) internalposition.x = reCalc(layerimagedata.x);
        if (layerimagedata?.y || layerimagedata?.y == 0) internalposition.y = reCalc(layerimagedata.y);
        if (layerimagedata?.width) internalposition.width = reCalc(layerimagedata.width);
        if (layerimagedata?.height) internalposition.height = reCalc(layerimagedata.height);
        if (layerimagedata?.rotation) internalposition.rotation = layerimagedata.rotation;


        if (layerimagedata?.rotation) internalposition.r = layerimagedata.rotation;


        if ((layerimagedata?._x || layerimagedata?._x == 0) && layerimagedata._imgwidth) {
            internalposition.x = reCalc(layerimagedata._imgx);
            internalposition.y = reCalc(layerimagedata._imgy);
            internalposition.width = reCalc(layerimagedata._imgwidth);
            internalposition.height = reCalc(layerimagedata._imgheight);
            delete layerimagedata._imgx;
            delete layerimagedata._imgy;
            delete layerimagedata._imgwidth;
            delete layerimagedata._imgheight;
        }
    }

    function reCalc(num,rev) {

        let p = parseFloat(zoom) / 100 / ratio

        let newCoord;
        if(rev){
            newCoord = num / p;
        } else {
            newCoord = num * p;
        }
        return newCoord
    }

    function isDragging(pointer) {
        return pointer && pointer.target && pointer.target.type === 'body' && pointerCount === 1
    }

    function isRotating(pointer) {
        return pointer && pointer.target && (pointer.target.type === 'rotate' || (pointer.target.type === 'handle' && handleMode === 'rotate'))
    }

    function isResizing(pointer) {
        return pointer && pointer.target && pointer.target.type === 'handle'
    }

    function isFreeTransforming(pointer) {
        return pointer && pointer.target && pointer.target.type !== 'handle' && pointerCount > 1
    }

    $: {
        console.log("position changed!!!!",internalposition);
        position = noZoomPosition({...internalposition});
        internalposition.style = stylePosition(internalposition)
        //onBoundsChange()
    }
    /*    $: {
            if(layerimagedata && layerimagedata.url){
                console.log("layerimagedata.url changed",layerimagedata.url);
            }
        }*/

    $: if (zoom && zoom != lastzoom) {
        lastzoom = zoom;
        recalculatePosition();
        internalposition.style = stylePosition(internalposition)
    }

    const stylePropMap = {
        x: 'left',
        y: 'top',
        width: 'width',
        height: 'height'
    }

    // Convert position to a CSS style string
    function stylePosition(pos) {

        return 'position: absolute; ' + ['x', 'y', 'width', 'height'].map(prop => {
            if (pos[prop] != null) return `${stylePropMap[prop]}: ${pos[prop]}px; `
            else return ''
        }).join(' ') + `; transform: rotate(${pos.rotation}rad)`
    }

    // Calculate the changes required for a resize gesture:
    // Scales the width and height of the component such that the same points in
    // component-space lie under the pointer and anchor point before and after the
    // transformation.
    // Does not preserve aspect ratio.
    function calcResizeTransform(pointer, anchor) {
        const rotation = startPosition.rotation
        const origin = bounds.center
        const topLeft = vector.sub(origin, {x: (startPosition.width / 2), y: (startPosition.height / 2)})
        const unrotatedEnd = vector.rotate(pointer.end, -rotation, anchor)
        const width = unrotatedEnd.x - anchor.x
        const height = unrotatedEnd.y - anchor.y
        const newOrigin = vector.add(topLeft, {x: Math.abs(width / 2), y: Math.abs(height / 2)})
        const unrotatedAnchor = vector.add(newOrigin, {x: -(width / 2), y: -(height / 2)})
        const newAnchor = vector.rotate(unrotatedAnchor, rotation, newOrigin)

        const translate = vector.sub(anchor, newAnchor)
        return {width, height, translate}
    }

    // Calculate the changes required for a free transform gesture:
    // Given two pointers, the component is scaled, rotated and translated
    // such that the same points in component-space lie under the pointers
    // before and after the transformation\
    //
    // Preserves aspect ratio.
    //
    // pointerB may be given as a single coord, instead of a { start, end } map,
    // allowing operations like 'rotate about the center' or 'resize from a corner'
    // to be performed using the same transform logic
    //
    // noRotate=true locks the current component rotation
    // noScale=true locks the current component width and height
    function calcFreeTransform(pointerA, pointerB, origin, noRotate, noScale) {
        // Handle the case where pointerB is a static point
        const pointerBStart = pointerB.start ? pointerB.start : pointerB
        const pointerBEnd = pointerB.end ? pointerB.end : pointerB

        const startAB = vector.sub(pointerBStart, pointerA.start)
        const endAB = vector.sub(pointerBEnd, pointerA.end)
        const scale = vector.length(endAB) / vector.length(startAB)
        const rotate = noRotate ? 0 : vector.direction(startAB) - vector.direction(endAB)

        const center = vector.add(vector.rotate(vector.scale(vector.sub(origin, pointerBStart), scale), rotate, {
            x: 0,
            y: 0
        }), pointerBEnd)
        const translate = vector.sub(center, origin)
        return {scale: (noScale ? 1 : scale), rotate, translate, center}
    }

    // Retrieve a DOMRect from the supplied element
    // and extend with x, y, and center fields
    function getFullBounds(element) {
        const elementBounds = element.getBoundingClientRect()
        elementBounds.x = elementBounds.left
        elementBounds.y = elementBounds.top
        elementBounds.center = {
            x: (elementBounds.left + elementBounds.right) / 2,
            y: (elementBounds.top + elementBounds.bottom) / 2
        }
        return elementBounds
    }

    export function changePosition() {
        if (internalposition.opacity != void 0) layerimagedata.opacity = internalposition.opacity;
        internalposition.style = stylePosition(internalposition);
        dispatch('positionChanged', internalposition)
    }


    // Apply a partial drag gesture
    // Simple translation of the component by the same distance as the pointer
    function doDrag(pointer) {
        internalposition.y = startPosition.y + (pointer.end.y - pointer.start.y)
        internalposition.x = startPosition.x + (pointer.end.x - pointer.start.x)
        let newPos = {
            x: startPosition.x + (pointer.end.x - pointer.start.x),
            y: startPosition.y + (pointer.end.y - pointer.start.y),
            width: internalposition.width,
            height: internalposition.height
        };

        // Apply snapping
        magneticLines = []
        const snappedPosition = getClosestSnappingPositionWithRotation(layers, newPos, magneticTolerance)

        internalposition.x = snappedPosition.x
        internalposition.y = snappedPosition.y

    }

    // Calculate and apply a partial resize gesture
    // Resizes the width/height of a component relative to an anchor point
    function doResize(pointer, anchor) {
        const transform = calcResizeTransform(pointer, anchor, bounds.center)
        internalposition.x = startPosition.x + transform.translate.x
        internalposition.y = startPosition.y + transform.translate.y
        internalposition.width = Math.abs(transform.width)
        internalposition.height = Math.abs(transform.height)
    }

    // Calculate and apply a partial free transform gesture
    function doFreeTransform(pointerA, pointerB, noRotate, noScale) {
        const transform = calcFreeTransform(pointerA, pointerB, bounds.center, noRotate, noScale)
        internalposition.width = startPosition.width * transform.scale
        internalposition.height = startPosition.height * transform.scale

        const tlToOrigin = vector.sub(bounds.center, startPosition)
        const tlvec = vector.add(vector.add(tlToOrigin, transform.translate), {
            x: -(internalposition.width / 2),
            y: -(internalposition.height / 2)
        })
        internalposition.x = startPosition.x + tlvec.x - containerBounds.left
        internalposition.y = startPosition.y + tlvec.y - containerBounds.top
        internalposition.rotation = startPosition.rotation + transform.rotate
    }

    // Get the target of a given pointer event
    function getPointerTarget(evt) {
        if (handleTR.contains(evt.target)) {
            return {type: 'handle', top: true, right: true}
        } else if (handleTL.contains(evt.target)) {
            return {type: 'handle', top: true, left: true}
        } else if (handleBR.contains(evt.target)) {
            return {type: 'handle', bottom: true, right: true}
        } else if (handleBL.contains(evt.target)) {
            return {type: 'handle', bottom: true, left: true}
        } else if (rotatorHandle && rotatorHandle.contains(evt.target)) {
            return {type: 'rotate'}
        } else return {type: 'body'}
    }

    function pointerDown(evt) {
        let pointerTarget = getPointerTarget(evt)
        let pointer = pointers[evt.pointerId]

        // Add the pointer to active pointers map
        if (!pointer) {
            pointerCount++
            pointer = {
                id: evt.pointerId,
                priority: pointerCount - 1,
                start: {
                    x: evt.pageX,
                    y: evt.pageY
                },
                end: {
                    x: evt.pageX,
                    y: evt.pageY
                },
                target: pointerTarget
            }
            pointers[evt.pointerId] = pointer
        } else { // or update, if it's already there
            pointer.end.x = evt.pageX
            pointer.end.y = evt.pageY
            pointer.target = pointerTarget
        }

        transformInProgress = false

        // If the pointer is enabled (one of the first two active pointers)
        if (pointer.priority < 2) {
            bounds = getFullBounds(target)
            containerBounds = getFullBounds(container)

            // There's another active enabled pointer
            if (pointerCount > 1) {
                // Starting a new multitouch gesture
                Object.values(pointers).forEach(pointer => {
                    if (pointer.id !== pointer)
                        pointer.start.x = pointer.end.x
                    pointer.start.y = pointer.end.y
                })
            }

            // Record the current component state as the gesture starting state
            startPosition.x = internalposition.x
            startPosition.y = internalposition.y
            startPosition.width = internalposition.width
            startPosition.height = internalposition.height
            startPosition.rotation = internalposition.rotation

            // If the pointer is dragging a corner handle, record the position of the
            // opposite corner (to anchor resizing events)
            if (pointerTarget.type === 'handle') {
                let oppositeHandle
                if (pointerTarget.top) {
                    if (pointerTarget.right) oppositeHandle = handleBL
                    else oppositeHandle = handleBR
                } else {
                    if (pointerTarget.right) oppositeHandle = handleTL
                    else oppositeHandle = handleTR
                }
                // handles are centered on the corresponding corner of the component frame
                // so we can quickly find the frame corner by calculating the center
                // of the on-screen bounds of the handle element
                // (easier than messing with trigonometry when the component is rotated)
                const oppositeHandleBounds = oppositeHandle.getBoundingClientRect()
                const oppositeCorner = {
                    x: (oppositeHandleBounds.left + oppositeHandleBounds.right) / 2,
                    y: (oppositeHandleBounds.top + oppositeHandleBounds.bottom) / 2
                }
                pointers.anchor = oppositeCorner
            } else {
                delete pointers.anchor
            }
        }
        onChange()
    }

    function pointerMove(evt) {
        endDrag = false;
        let pointer = pointers[evt.pointerId]
        // Ignore unless pointer is enabled (one of the first two active pointers)
        if (pointer && pointer.priority < 2) {
            transformInProgress = true

            // update the on-screen position
            pointer.end.x = evt.pageX
            pointer.end.y = evt.pageY

            // Perform different transformations depending which part of the component is
            // being dragged/manipulated
            if (isDragging(pointer))
                doDrag(pointer);

            else if (isRotating(pointer)) doFreeTransform(pointer, bounds.center, false, true)
            else if (isResizing(pointer) && !lockAspect) doResize(pointer, pointers.anchor)
            else if (isResizing(pointer) && lockAspect) doFreeTransform(pointer, pointers.anchor, true)
            else if (isFreeTransforming(pointer)) {
                let multitouch = pointerCount > 1
                let pointerB
                if (multitouch) {
                    pointerB = Object.values(pointers).find(p => {
                        return p.id !== pointer.id
                    })
                } else if (pointers.anchor) {
                    pointerB = pointers.anchor
                } else {
                    pointerB = bounds.center
                }
                doFreeTransform(pointer, pointerB, false, (multitouch && lockTouchResize))
            }


            onChange()
        }
    }

    function pointerUpInside() {
        if (transformInProgress) {
            endDrag = true;
            lockAspect = false;
            transformInProgress = false;
            dispatch('positionChanged', internalposition)
        }
    }


    // Clean up after a pointer is removed and the gesture is ended
    function pointerUp(evt) {

        let removedPriority = Infinity
        if (pointers[evt.pointerId]) {
            pointerCount--
            removedPriority = pointers[evt.pointerId].priority
        }
        // remove the pointer from the active pointers map
        delete pointers[evt.pointerId]
        // If there are still some other active pointers
        if (pointerCount) {
            // Reset the startPosition to start a new gesture
            startPosition.y = internalposition.y
            startPosition.x = internalposition.x
            startPosition.width = internalposition.width
            startPosition.height = internalposition.height
            startPosition.rotation = internalposition.rotation
            // update the enabled pointer priority queue
            // and reset the pointer start positions for a new gesture
            Object.values(pointers).forEach(pointer => {
                if (pointer.id) {
                    if (pointer.priority > removedPriority) pointer.priority--
                    pointer.start.x = pointer.end.x
                    pointer.start.y = pointer.end.y
                }
            })
        }

    }

    function contentClick(evt) {
        if (!transformInProgress) {
            dispatch('click', evt.detail)
        }
    }

    async function onChange() {
        await tick()
        if (element) {
            selfBounds = element.getBoundingClientRect();
        }
        dispatch('positionChanged', internalposition)

    }

    async function onBoundsChange() {
        await tick()
        if (element) {
            selfBounds = element.getBoundingClientRect()
            dispatch('boundsChanged', internalposition);
            if (!transformArea.classList.contains('fade-in-div')) {
                // If the class doesn't exist, add it
                transformArea.classList.remove('faded');
                transformArea.classList.add('fade-in-div');
            }
        }
    }
</script>


<svelte:window
        on:pointermove={pointerMove}
        on:pointerup={pointerUp}
        on:keyup={onKeyUp}
        on:keydown={onKeyDown}
/>
{#if imageurl || layerimagedata.url}

    <!-- svelte-ignore a11y-click-events-have-key-events -->
    <div on:pointerup={pointerUpInside} on:click={contentClick} class="faded transform-area" bind:this={transformArea}>
        <div
                class="free-transform"
                class:noclicks={!captureClicks}
                on:pointerdown={pointerDown}
                bind:this={element}
                style={selfStyled ? internalposition.style : null}
        >
            <div class="contents" style="opacity:{(layerimagedata.opacity/100)}">
                {#if imagesvg}
                    {@html imagesvg}
                {:else}
                    <!-- svelte-ignore a11y-missing-attribute -->
                    <img style="width: 100%;height: 100%" bind:this={imageel} src={imageurl || layerimagedata.url}>
                {/if}
            </div>
            <div style="width:80px; height:50px; position: sticky; z-index: 5; bottom: 0px; left:0px; align-self:flex-end;">
                <slot></slot>
            </div>
            <div class="frame">
                <div class="handle corner top right" bind:this={handleTR}>
                    <slot name="handle">
                        <div class="handle-shape"/>
                    </slot>
                </div>
                <div class="handle corner bottom right" bind:this={handleBR}>
                    <slot name="handle">
                        <div class="handle-shape"/>
                    </slot>
                </div>
                <div class="handle corner bottom left" bind:this={handleBL}>
                    <slot name="handle">
                        <div class="handle-shape"/>
                    </slot>
                </div>
                <div class="handle corner top left" bind:this={handleTL}>
                    <slot name="handle">
                        <div class="handle-shape"/>
                    </slot>
                </div>
                {#if rotatorEnabled }
                    <div class="handle rotator" bind:this={rotatorHandle}>
                        <slot name="rotator">
                            <div class="handle-shape"/>
                        </slot>
                        {#if rotatorBar }
                            <div class="rotator-bar"/>
                        {/if}
                    </div>
                {/if}
            </div>
        </div>
        <div class="fullwidthheight" style="display: none"/>
        {#if demo}
            <div style="position:absolute;top:100px;left:100px;width:400px;height:400px;border: 1px solid grey;transform:rotate(-45deg);transform-origin: center center"></div>
            <!--     <div style="position:absolute;top:100px;left:100px;width:400px;height:400px;border: 1px solid grey;"></div> -->

        {/if}
        {#each magneticLines as line}
            <div
                    class="line"
                    style="
        --x-line: {line.x_line}px;
        --y-line: {line.y_line}px;
        --line-length: {line.lineLength}px;
        --theta-line: {line.theta_line}rad;
        "
            ></div>
        {/each}
    </div>

{/if}

<style>
    :root {
        --frameBorder: solid 1px cyan;
    }

    * {
        box-sizing: border-box;
    }

    .free-transform {
