        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        transform-origin: center;
        touch-action: none;
        display: flex;
    }

    .free-transform.noclicks {
        pointer-events: none;
    }

    .contents {
        /*border: var(--frameBorder,2px solid #3965c7);*/
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }

    .contents:before {
        content: "";
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100%;
        height: 100%;
        border: var(--frameBorder, 2px solid #3965c7);
        box-sizing: border-box;
    }


    .free-transform.noclicks .contents {
        pointer-events: none;
    }

    .frame {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        cursor: move;
        /*border: var(--frameBorder,2px solid #3965c7);*/
    }

    .free-transform.noclicks .frame {
        pointer-events: none;
    }

    .handle {
        position: absolute;
        user-select: none;
        touch-action: none;
        cursor: grab;
        pointer-events: auto;
    }

    .handle:active {
        cursor: grabbing;
    }

    .handle-shape {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        background: white;
        border: var(--frameBorder);
    }

    .handle.top {
        top: 0px;
    }

    .handle.right {
        right: 0px;
    }

    .handle.bottom {
        bottom: 0px;
    }

    .handle.left {
        left: 0px;
    }

    .handle.top.left {
        transform: translate(-50%, -50%) rotate(-45deg);
    }

    .handle.top.right {
        transform: translate(50%, -50%) rotate(45deg);
    }

    .handle.bottom.left {
        transform: translate(-50%, 50%) rotate(-135deg);
    }

    .handle.bottom.right {
        transform: translate(50%, 50%) rotate(135deg);
    }

    .handle.rotator {
        left: 50%;
        top: -30px;
        transform: translateX(-50%);
    }

    .rotator-bar {
        position: absolute;
