    }

    .fade-in-div {
        opacity: 1;
        animation: fadeIn 0.2s forwards;
        animation-delay: 0.7s;
    }

    @keyframes fadeIn {
        to {
            opacity: 1;
        }
    }

    .line {
        position: absolute;
        width: var(--line-length);
        height: 1px;
        left: var(--x-line);
        top: var(--y-line);
        transform: translate(-50%, -50%) rotate(var(--theta-line));
        transform-origin: center center;
        background-color: red;
    }

</style>
