export default class magneticLines {

    rotatePoint(px, py, angle) {
        const cosTheta = Math.cos(angle);
        const sinTheta = Math.sin(angle);
        return {
          x: px * cosTheta - py * sinTheta,
          y: px * sinTheta + py * cosTheta
        };
      }
    calcMagneticLine(side, linePosition, lineOrientation, box) {
          let lineCenter;
          let theta_line;
          let x_line;
          let y_line;
          let lineLength;
          let extendLength = 500; // Extension length beyond the rectangle
  
          // Extract box properties
          let x = box.x;
          let y = box.y;
          let width = box.width;
          let height = box.height;
          let theta = box.rotation;
  
          // Calculate the center of the rectangle
          let x_center = x + width / 2;
          let y_center = y + height / 2;
  
          if (linePosition === 'side') {
              // Line along the side
              if (side === 'top') {
              lineCenter = this.rotatePoint(0, -height / 2, theta);
              theta_line = theta;
              lineLength = width + 2 * extendLength;
              } else if (side === 'right') {
              lineCenter = this.rotatePoint(width / 2, 0, theta);
              theta_line = theta + Math.PI / 2;
              lineLength = height + 2 * extendLength;
              } else if (side === 'bottom') {
              lineCenter = this.rotatePoint(0, height / 2, theta);
              theta_line = theta + Math.PI;
              lineLength = width + 2 * extendLength;
              } else if (side === 'left') {
              lineCenter = this.rotatePoint(-width / 2, 0, theta);
              theta_line = theta - Math.PI / 2;
              lineLength = height + 2 * extendLength;
              }
              // @ts-ignore
              x_line = x_center + lineCenter.x;
              // @ts-ignore
              y_line = y_center + lineCenter.y;
          } else if (linePosition === 'center') {
              // Line through the center
              x_line = x_center;
              y_line = y_center;
  
              if (lineOrientation === 'horizontal') {
              theta_line = theta; // Aligned with rectangle's top and bottom edges
              } else if (lineOrientation === 'vertical') {
              theta_line = theta + Math.PI / 2; // Aligned with rectangle's left and right edges
              } else if (lineOrientation === 'aligned') {
              theta_line = theta; // Same as 'horizontal' in this context
              }
              // Calculate length sufficient to cover rectangle
              const diagonal = Math.hypot(width, height)
              lineLength = diagonal + 2 * extendLength
          }
          return { x_line, y_line, lineLength, theta_line }
      }
  
    /**
     * Converts the normalized snapped position back to the original coordinates before normalization.
     * Uses the shared center and sharedRotation from rotateBoxesToZero.
     * 
     * @param {Object} normalizedSnappedPosition - The snapped position after normalization (x, y only)
     * @param {Number} originalRotation - The rotation that was applied during normalization
     * @returns {Object} - The transformed position with correct original x, y coordinates
     */
    rotatePositionBackToOriginal(normalizedSnappedPosition, originalRotation,) {
        let centerX=this.centerX
        let centerY=this.centerY
        // Extract normalized position and dimensions
        const snappedX = normalizedSnappedPosition.x;
        const snappedY = normalizedSnappedPosition.y;
        const width = normalizedSnappedPosition.width;
        const height = normalizedSnappedPosition.height;

        // Calculate the center of the box in normalized coordinates
        const boxCenterX = snappedX + width / 2;
        const boxCenterY = snappedY + height / 2;

        // Rotate the center back by originalRotation around the centroid
        // @ts-ignore
        const rotatedCenterX = centerX + (boxCenterX - centerX) * Math.cos(originalRotation) - (boxCenterY - centerY) * Math.sin(originalRotation);
        // @ts-ignore
        const rotatedCenterY = centerY + (boxCenterX - centerX) * Math.sin(originalRotation) + (boxCenterY - centerY) * Math.cos(originalRotation);

        // Calculate the new top-left position
        const rotatedX = rotatedCenterX - width / 2;
        const rotatedY = rotatedCenterY - height / 2;

        // Return the original position after rotating back
        return {
            x: rotatedX,
            y: rotatedY,
            width: width,
            height: height,
            rotation: originalRotation
        };
    }


    /**
     * rotation of list of layers/boxes so that rotation is zero but arragement of all boxes remains same
     * @param boxes
     * @param sharedRotation
     */
     rotateBoxesToZero(boxes, sharedRotation) {
        // Calculate the center point of the system, could be an average of all centers or a specific point.
        let centerX = 0
        let centerY = 0

        boxes.forEach(box => {
            centerX += box.x + box.width / 2
            centerY += box.y + box.height / 2
        });

        centerX /= boxes.length
        centerY /= boxes.length
        this.centerX=centerX
        this.centerY=centerY
        // Rotate all boxes by the negative of the shared rotation
        boxes.forEach(box => {
            let boxCenterX = box.x + box.width / 2
            let boxCenterY = box.y + box.height / 2

            let newX = centerX + (boxCenterX - centerX) * Math.cos(-sharedRotation) - (boxCenterY - centerY) * Math.sin(-sharedRotation)
            let newY = centerY + (boxCenterX - centerX) * Math.sin(-sharedRotation) + (boxCenterY - centerY) * Math.cos(-sharedRotation)

            // Update the box position by adjusting for the new center point
            box.x = newX - box.width / 2
            box.y = newY - box.height / 2

            // Set rotation to 0
            box.rotation = 0
        })

        return boxes
    }
}