<svelte:options tag="fds-coord-dialog"></svelte:options>
<script>
    import {get_current_component} from "svelte/internal"
    import {clickOutside} from "./clickOutside";
    import {toollayer} from "./stores";
    import {onMount} from 'svelte'
    let host = get_current_component().$$.root.host
    let foldertype = false;
    export let coordinates = {
        x: 0,
        y: 0,
        width: 0,
        height: 0,
        rotation: 0
    }


    function change(name, value) {
        if (name!='flipHorizontal' && name!='flipVertical' && name!='foldermovedone' && name!='foldermovecancel'  && isNaN(value)) return;

        if (name!='foldermovedone' && name!='foldermovecancel' && name!='destructmovetool'){
            $toollayer[name] = parseInt(value);
        }


        if (name == 'rotation' || name == 'opacity') {
            $toollayer[name] = parseFloat(value);
        }
        host.dispatchEvent(new CustomEvent("change", {detail: {name: name, value: value}}))
    }

    $: if ($toollayer) {
        refresh();
    }

    $: if(globalThis.gyre && globalThis.gyre.paletteValues.selectedLayer && globalThis.gyre.paletteValues.selectedLayer.type=='folder'){
        foldertype = true;
    }

    onMount(() => {
       return ()=>{
           change('destructmovetool',1);
           setTimeout(() => {
               if(globalThis.gyre && globalThis.gyre.paletteValues.selectedLayer){
                   //globalThis.gyre.paletteValues.selectedLayer.nocache = false;
                   globalThis.gyre.layerManager.traverseLayers((layer)=> {
                       layer.nocache = false;
                   })
               }
           }, 40);
       }
    })


    function handleClickOutside() {
        //editcoord=false
    }


    export let editcoord

    export function refresh() {
        coordinates = $toollayer;
    }
</script>

<div class="show" style="display: inline-flex;">
    {#if coordinates}
        <div style="display:flex;align-items: center">
            <!-- svelte-ignore a11y-click-events-have-key-events -->
            <fds-image-editor-button
                    icon="flipHorizontal"
                    title="Flip Horizontal"
                    on:click={() => {
                change('flipHorizontal')
            }}
            />

            <!-- svelte-ignore a11y-click-events-have-key-events -->
            <fds-image-editor-button
                    icon="flipVertical"
                    title="Flip Vertical"
                    on:click={() => {
                  change('flipVertical')
            }}
            />
            <!-- svelte-ignore a11y-click-events-have-key-events -->
            <fds-image-editor-button type="button"
                                     on:click={() => { editcoord=!editcoord; change('editcoord',editcoord); }}>
                Coordinates
            </fds-image-editor-button>
            {#if foldertype}
                <fds-image-editor-button type="button"
                                         on:click={() => {   change('foldermovedone',1); }}>
                    Done
                </fds-image-editor-button>
                <fds-image-editor-button type="button"
                                         on:click={() => {   change('foldermovecancel',1); }}>
                   Cancel
                </fds-image-editor-button>


            {/if}

        </div>
    {/if}

    {#if editcoord && coordinates }
        <div class="editcoord" use:clickOutside on:click_outside={handleClickOutside}>
            <div class="formRow">

                <div class="formLine">
                    <label for="x" class="more-label">X:</label>
                    <input value={parseInt(coordinates.x)}
                           on:change={(e) => {coordinates.x=e.target.value;coordinates=coordinates;change("x",e.target.value) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>

                <div class="formLine">
                    <label for="y" class="more-label">Y:</label>
                    <input value={parseInt(coordinates.y)}
                           on:change={(e) => { coordinates.y=e.target.value;coordinates=coordinates;change("y",e.target.value) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>
            </div>
            <div class="formRow">
                <div class="formLine">
                    <label for="width" class="more-label">Width:</label>
                    <input value={parseInt(coordinates.width)}
                           on:change={(e) => {  coordinates.width=e.target.value;coordinates=coordinates;change("width",e.target.value) }}
                           type="text" class="formInput" placeholder=""
                           style="font-size: 12px; width: 220px; display: inline-block;">
                </div>

                <div class="formLine">
                    <div class="formLine">
                        <label for="height" class="more-label">Height:</label>
                        <input value={parseInt(coordinates.height)}
                               on:change={(e) => { coordinates.height=e.target.value;coordinates=coordinates;change("height",e.target.value) }}
                               type="text" class="formInput" placeholder=""
                               style="font-size: 12px; width: 220px; display: inline-block;">
                    </div>
                </div>
            </div>

            <div class="formLine">
                <label for="rotation" class="more-label">Rotation:</label>
                <input value={((coordinates.rotation * 180.0) / Math.PI).toFixed(2)}
                       on:change={(e) => { coordinates.rotation=e.target.value;coordinates=coordinates;change("rotation", parseInt(e.target.value) * (Math.PI/180)) }}
                       type="text" class="formInput" placeholder=""
                       style="font-size: 12px; width: 220px; display: inline-block;">°
            </div>


        </div>
    {/if}

</div>

<style>
    * {
        box-sizing: border-box;
    }


    .editcoord {
        min-width: 330px;
        min-height: 100px;
        box-shadow: 0 0 1rem 0 rgba(255, 255, 255, .2);
        border-radius: 8px;
        background-color: rgba(0, 0, 0, .65);
        backdrop-filter: blur(10px);
        color: rgba(255, 255, 255, 0.6);
        position: absolute;
        left: 0px;
        top: 50px;
        padding: 14px;
        font-family: "Segoe UI", Roboto, system-ui;
        z-index: 2000;
    }

    .formLine {
        display: flex;
        align-items: center;
        margin-bottom: 5px;
    }

    .formLine label {
        margin-right: 10px;
        width: 60px; /* Adjust the width as needed */
    }

    .formLine input {
        flex: 1;
        min-width: 0;
    }

    .formRow {
        display: flex;
        justify-content: space-between;
        gap: 20px; /* Space between the two sets of inputs */
        margin-bottom: 20px;
    }

    .formInput {
        width: 100%;
        max-width: 80px;
        outline: 0;
        border: 0;
        border-bottom: 2px solid rgba(255, 255, 255, 0.2);
        padding: 5px;
        font-size: 14px;
        font-weight: normal;
        border-radius: 3px;
        color: rgba(255, 255, 255, 0.9);
        margin-bottom: 10px;
        display: block;
        background: none;
        font-family: system-ui, "Segoe UI", Roboto;

    }

    .show {
        display: block;
    }


</style>